<template>
  <div>
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>Comisson</h1>
      <div class="chartWrap">
        <a href="javascript:void(0)" @click="show = true" class="iconfont cart">
          &#xe6e9;
        </a>
      </div>
    </section>
    <div class="center">
          <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
            <van-list
              v-model="loading"
              :finished="finished"
              finished-text="The end"
              @load="onLoad"
            >
      <ul>
        <li v-for="(item, index) in commissionList" :key="index">
          <div class="generated">
            <div class="coat">
              <span class="title">{{ item.bizDesc }}</span
              ><span class="value">{{ item.createDate }}</span>
            </div>
            <div class="content">
              <div class="name_left">
                <span
                  >Order User: <font>{{ item.userName }}</font></span
                >
                <span
                  >Order Sum: <font>{{ item.orderSum }}AED</font></span
                >
              </div>
              <div
                class="aed_right"
                :style="{ color: item.changeType == 1 ? '#E33946' : '#333333' }"
              >
                {{ item.changeType == 1 ? "+" : "-" }}{{ item.amount }} AED
              </div>
            </div>
          </div>
        </li>
      </ul>
            </van-list>
          </van-pull-refresh>
      <van-popup
        v-model="show"
        position="rigth"
        style="height: 100%; width:80%;"
      >
        <div class="selectTimeWrap">
          <div class="selectTime">
            <div class="blocks">
              <p>Select Time</p>
              <div class="tags row">
               <div @click="selectTime(0)">{{startTime}}</div>
                <div class="colM">—</div>
               <div @click="selectTime(1)">{{endTime}}</div>
              </div>
            </div>
            <div class="radioBlocks">
              <p>
                <span>Commission Types</span>
                <span class="iconfont red">&#xe671;</span>
              </p>
              <div>
                <ul>
                  <li
                    :class="[
                      index == type ? 'red' : '',
                      index == type ? '' : ''
                    ]"
                    @click="type = index"
                    v-for="(item, index) in commissionTypes"
                    :key="index"
                  >
                    <label>{{ item }}</label>
                    <span class="iconfont"> {{ index == type ? "&#xe607;" : "&#xe608;" }}
                    </span>
                  </li>
                </ul>
              </div>
            </div>
            <div class="downBtnWrap">
              <van-button
                @click="
                  (startTime = 'Start Time'), (endTime = 'End Time'), (type = null);
                  show = false;
                "
                type="danger"
                >Reset</van-button
              >
              <van-button
                @click="
                  init();
                  show = false;
                "
                type="danger"
                >Confirm</van-button
              >
            </div>
          </div>
        </div>
      </van-popup>
    </div>
    
      <calendar :show.sync="showCalender" v-model="selectDate" mode="single" @change="onChange"/>
  </div>
</template>

<script>
import { commission } from "@/api/store";
import { mapGetters } from "vuex";
import Vue from "vue";
import Calendar from "vue-mobile-calendar";
Vue.use(Calendar);
export default {
  name: "comisson",
  data() {
    return {
      commissionList: {},
      show: false,
      show2: false,
      startTime: "Start Time",
      endTime: "End Time",
      selectDate: null,
      type: null,
      selectIndex: 0,
      showCalender: false,
      commissionTypes: ["Order Generated", "Case Withdraw", "User Register"],
      loading: false,
      finished: false,
      isLoading: true,
      query: {}
    };
  },
  computed: {
    ...mapGetters(["indexData"])
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.query = Object.assign(this.query, { pageNo: 1, pageSize: 10 });
      commission({
        pageNo: this.query.pageNo,
        pageSize: this.query.pageSize,
        type: this.type != null ? this.type + 1 : null,
        startDate: this.startTime != "Start Time" ? this.startTime : null,
        endDate: this.endTime != "End Time" ? this.endTime : null
      })
        .then(res => {
          this.commissionList = res.data.result.result;
          this.isLoading = false;
          if (res.data.result.totalNum <= this.commissionList.length) {
            this.finished = true;
          }
        })
        .catch(e => {
          this.finished = true;
          this.isLoading = false;
        });
    },
    onLoad() {
      if (this.isLoading) {
        this.loading = false;
        return false;
      }
      this.query.pageNo++;
     commission({
        pageNo: this.query.pageNo,
        pageSize: this.query.pageSize,
        type: this.type != null ? this.type + 1 : null,
        startDate: this.startTime != "Start Time" ? this.startTime : null,
        endDate: this.endTime != "End Time" ? this.endTime : null
      })
        .then(res => {
          this.commissionList = this.list.concat(res.data.result.result);
          this.loading = false;
          if (res.data.result.totalNum <= this.commissionList.length) {
            this.finished = true;
          }
        })
        .catch(e => {
          this.finished = true;
          this.loading = false;
        });
    },
    onRefresh() {
      this.init();
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    },
    fomrateTime(date) {
      return (
        date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate()
      );
    },
    onChange(date) {
      if (this.selectIndex == 0) {
        this.startTime = date.format("YYYY-MM-DD");
      } else {
        this.endTime = date.format("YYYY-MM-DD");
      }
      this.showCalender = false;
    },
    selectTime(index) {
      this.selectIndex = index;
      this.showCalender = true;
    }
  }
};
</script>
<style lang="scss" scoped>
.el-picker-panel {
  z-index: 2600 !important;
  width: 100%;
  display: block !important;
  bottom: 0;
  top: 46% !important;
}
.row {
  .el-input__inner {
    padding: 0;
    -webkit-box-flex: 0;
    -ms-flex: 0 0 42%;
    flex: 0 0 42%;
    width: 111.3px;
    height: 28px;
    border: #666666 1px solid;
    border-radius: 2px;
    text-align: center;
  }
  .el-input__prefix {
    display: none;
  }
}
.el-input {
  width: 111.3px !important;
}
.blocks {
  input::-webkit-input-placeholder {
    color: #666666;
  }
}
.el-input__suffix-inner {
  display: none;
}
.el-date-picker .el-picker-panel__content {
  width: 92%;
}
</style>
<style lang="scss" scoped>
.center {
  .van-popup {
    left: 60%;
  }
  .withdraw {
    border: #f6f6f6 1px solid;
    padding: 0 4%;
    .coat {
      width: 100%;
      padding: 10px 0 0 0;
      display: -webkit-box;
      span {
        width: 50%;
        display: block;
        color: #999999;
      }
      .value {
        text-align: right;
      }
      .title {
        font-size: 15px;
        color: #333333;
        font-weight: 500;
      }
    }
    .aedWrap {
      text-align: right;
      font-size: 15px;
      padding-bottom: 10px;
      color: #333333;
    }
  }
  ul {
    width: 100%;
    border-top: 10px solid #f6f6f6;
    border-bottom: 10px solid #f6f6f6;
  }
  li {
    width: 100%;
    padding-left: 4%;
    .register,
    .generated,
    .cancelled {
      border: #f6f6f6 1px solid;
      padding: 0 4% 0 0;
      .coat {
        width: 100%;
        padding: 10px 0 0 0;
        display: -webkit-box;
        span {
          width: 50%;
          display: block;
          color: #999999;
        }
        .value {
          text-align: right;
        }
        .title {
          font-size: 15px;
          color: #333333;
          font-weight: 500;
        }
      }
      .userAed {
        width: 100%;
        padding: 3px 0 10px 0;
        display: -webkit-box;
        span {
          width: 50%;
          display: block;
          color: #b7b7b7;
        }
        .value {
          text-align: right;
          color: #e33946;
          font-size: 15px;
        }
        .title {
          font-size: 13px;
          color: #666666;
          font {
            color: #333333;
            font-size: 14px;
          }
        }
      }
      .content {
        width: 100%;
        display: -webkit-box;
        padding: 5px 0 10px 0;
        .name_left {
          width: 50%;
          span {
            display: block;
            color: #666666;
            padding-top: 5px;
            font-size: 13px;
            font {
              color: #333333;
              font-size: 14px;
            }
          }
        }
        .aed_right {
          width: 50%;
          display: flex;
          align-items: center;
          justify-content: flex-end;
          color: #e33946;
          font-size: 15px;
        }
      }
    }
  }
}

.cancelled .content .aed_right {
  color: #333333 !important;
}
.selectTimeWrap {
  padding-bottom: 55px;
  overflow-y: scroll;
  padding-top: 20px;
  height: 100%;
  position: relative;
  .selectTime {
    font-size: 13px;
    background: #f4f4f4;
    .blocks {
      padding: 10px 15px 10px 20px;
      background-color: #fff;
      p {
        color: #333333;
        margin: 15px 0 10px;
        font-size: 15px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
      }
      .tags {
        a {
          display: block;
          color: #666666;
          border-radius: 2px;
          border: solid 1px #666666;
          line-height: 26px;
          width: 100%;
          text-align: center;
        }
        a.active {
          color: #e33946;
          border-color: #e33946;
        }
      }
      .row {
        display: flex;
        align-items: center;
        .col {
          flex: 0 0 42%;
        }
        .colM {
          flex: 0 0 8%;
          text-align: center;
        }
      }
    }
    .radioBlocks {
      padding: 10px 0;
      background-color: #fff;
      margin-top: 10px !important;
      p {
        color: #333333;
        margin-top: 5px;
        font-size: 15px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
        padding: 0 15px 15px 20px;
        border-bottom: solid 1px #e5e5e5;
      }
      ul {
        padding: 0 15px 0 20px;
        width: 100%;
        border-top: none;
        border-bottom: none;
        li {
          padding: 15px 0;
          border-bottom: solid 1px #e5e5e5;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-pack: justify;
          -ms-flex-pack: justify;
          justify-content: space-between;
        }
      }
    }
    .downBtnWrap {
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
      background: #fff;
      padding: 5px 25px 20px;
      display: flex;
      justify-content: space-between;
      .van-button {
        width: 45%;
        height: 32px;
        line-height: 30px;
      }
      .van-button:first-child {
        background-color: #fff;
        color: #e33946;
      }
    }
  }
}
div
  /deep/
  .m-calendar
  .m-months-container
  .m-months-wrapper
  .m-months
  .m-row
  .m-day
  .m-select {
  background: #e33946;
}
div
  /deep/
  .m-calendar
  .m-months-container
  .m-months-wrapper
  .m-months
  .m-row
  .m-day {
  font-size: 16px;
  color: #999999;
}
div
  /deep/
  .m-calendar
  .m-months-container
  .m-months-wrapper
  .m-months
  .m-row:before {
  height: 0;
}

div /deep/ {
  .m-popover .m-popover-container {
    height: unset;
  }
  .m-calendar
    .m-months-container
    .m-months-wrapper
    .m-months
    .m-row
    .m-day
    .m-today {
    background: none;
  }
  .m-calendar .m-week-header {
    font-size: 16px;
    .m-week-day {
      height: 36px;
      line-height: 36px;
    }
  }
  .m-calendar .m-toolbar .m-year-selector .m-prev-btn {
    background-image: url("../common/images/retun.png");
    width: 7px;
    height: 14px;
  }
  .m-calendar .m-toolbar .m-year-selector .m-next-btn {
    background-image: url("../common/images/next.png");
    width: 7px;
    height: 14px;
  }
}
</style>





